import productlist from '../../components/productlist/productlist.vue'
import request from '../../util/request.js'
export default {
	data() {
		return {
			swiperheight: 100,
			selecttext: "",
			swiperList: [{
				id: 0,
				type: 'image',
				url: 'https://img.alicdn.com/tfs/TB1cxAyRXY7gK0jSZKzXXaikpXa-520-280.jpg_q90_.webp'
			}, {
				id: 1,
				type: 'image',
				url: 'https://aecpm.alicdn.com/simba/img/TB1XotJXQfb_uJkSnhJSuvdDVXa.jpg',
			}, {
				id: 2,
				type: 'image',
				url: 'https://gw.alicdn.com/imgextra/i1/952917/O1CN01iVH3pY1XQ1viW7yeG_!!952917-0-lubanu.jpg'
			}, {
				id: 3,
				type: 'image',
				url: 'https://img.alicdn.com/tfs/TB1Va9UriMnBKNjSZFzXXc_qVXa-800-300.jpg'
			}],
			shops: [],
			dotStyle: true,
			curr: 0,
			cardCur: '',
			querylist: {
				pageNum: 1,
				pageSize: 50,
				query: ' '
			}
			// TabCur: 0,
			// scrollLeft: 0
		}
	},
	onLoad() {
		uni.getSystemInfo({
			success: (res) => {
				let height = res.windowHeight - uni.upx2px(100)
				this.swiperheight = height;
			}
		})
	},
	
	onShow() {
		var that = this;
		let ins = {
			url: '/skus',
			method: 'get'
		};
		let data = this.querylist;
		request.TokenRequest(ins, data).then(res => {
			that.shops = res.data.list;
			for (let i = 0; i < that.shops.length; i++) {
				that.shops[i].image = "http://123.57.7.108:81/" + that.shops[i].image;
			}
		}).catch(error => {
			console.log(error)
		});

	},
	components: {
		productlist
	},
	methods: {
		gotolist:function(){
			uni.navigateTo({
				url:"../List/List"
			})
		},
		//跳转页面
		gomessage: function(id) {
			uni.navigateTo({
				url: "../message/message?id=" + id
			})
		},
		
		select() {
			var that = this;
			console.log(this.selecttext)
			this.querylist.query=this.selecttext;
			let ins = {
				url: '/skus',
				method: 'get'
			};
			let data = this.querylist;
			request.TokenRequest(ins, data).then(res => {
				that.shops = res.data.list;
				for (let i = 0; i < that.shops.length; i++) {
					that.shops[i].image = "http://123.57.7.108:81/" + that.shops[i].image;
				}
			}).catch(error => {
				console.log(error)
			});
		},
		cardSwiper(e) {
			this.cardCur = e.detail.current
		},
		setCurr(e) {
			console.log(e.detail.current)
			let thisCurr = e.detail.current || e.currentTarget.dataset.index || 0;
			console.log(thisCurr)
			this.curr = thisCurr;
		},
		InputFocus() {
			console.log("lalla")
		},
		InputBlur() {
			uni.hideKeyboard();
			console.log("aaaaa")
		},
		clickImg(num) {
			console.log(num.id)
		}
	}
}
